.filters {
    display: flex;
    align-items: center;
    overflow: auto;
    &__item {
        &:not(:last-child) {
            margin-right: 10px;
        }
        &:first-child button {
            color: #fff;
            background-color: #282828;
        }
    }

    &__btn {
        padding: 14px 35px;
        background-color: #F9F9F9;
        border-radius: 30px;
        font-weight: 700;
        line-height: 20px;
        color: #2c2c2c;
    }
}