.sort  {
    position: relative;
    &__head {
        display: flex;
        align-items: center;
    }

    &__icon {
        margin-right: 7px;
        transition: transform 0.3s;
    }

    &__title {
        margin-right: 8px;
        font-size: 14px;
        line-height: 17px;
        font-weight: 700;
    }

    &__label {
    }

    &__dropdown {
        position: absolute;
        top: 100%;
        right: 0;
    }

    &__item {
        
    }
}

.react-select {
    &__control {
        border: none !important;
        border-radius: 0 !important;
        min-height: 0 !important;
        padding: 0 !important;
    }
    &__indicators {
        display: none !important;
    }

    &__value {
        &-container {
            padding: 0 !important;
        }
    }

    &__menu {
        width: 132px !important;
        top: 23px !important;
        right: 0;
        padding: 13px 0 !important;
        box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09) !important;
        border-radius: 10px !important;
    }

    &__single-value {
        font-size: 14px;
        color: #fe5f1e !important;
        padding-bottom: 1px;
        line-height: 17px;
        border-bottom: 1px dashed #FE5F1E;
        cursor: pointer !important;
    }

    &__option {
        cursor: pointer !important;
        padding: 10px 14px !important;
        &:active {
            color: inherit !important;
            background-color: #fff !important;
        }
    }
}